/* Main Page */

.main {
  text-align: center;
  height: 100vh;
  /*//display: flex;
  flex-direction: column;*/
  font-family: 'Inter', sans-serif;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.main-title {
  color: black;
  position: absolute;
  top: 30vh;
  width: 100%;
}

.main-title h1 {
  font-family: 'Old Standard TT', serif;
  font-size: calc(100px + 5vmin);
  margin: 0;
  letter-spacing: 20px;
  margin-right: -15px;
}

#h1-line {
  width: 300px;
  margin-top: 0;
}
.main-title p {
  font-size: calc(10px + 3vmin);
}

.page-title p {
  font-size: calc(10px + 3vmin);
  margin: 0;
}

.footer-section {
  position: absolute;
  bottom: 0px;
  width: 100%;
}

.project-footer-section {
  position: relative;
  bottom: 0px;
  width: 100%;
  text-align: center;
}

/* All Pages */

.project-title {
  display: flex;
  justify-content: space-between;
  padding: 20px 10px 20px 10px;
}

.page {
  text-align: center;
}

.page-title {
  text-align: center;
}

.page-subtitle {
  text-align: justify;
  margin: auto;
  padding: 20px;
  padding-top: 0;
  max-width: 400px;
}

.project {
  padding: 10px;
  text-align: left;
  max-width: 400px;
}
.nav {
  display: flex;
  justify-content: space-between;
}

.buttons {
  display: flex;
  justify-content: left;
  padding: 10px;
}

button {
  color: black;
  background-color: transparent;
  border: none;
  text-decoration: none;
  transition: text-decoration 0.3s ease-in-out;
  padding: 10px;
  font-size: calc(5px + 1vmin);
}

#button-pageCurrent {
  text-decoration: underline;
}

#button-qali {
  font-family: 'Old Standard TT', serif;
}

#button-qali:hover {
  text-decoration: none;
}

button:hover {
  text-decoration: underline;
}

.App-link {
  color: #61dafb;
}



.projects {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
}

.project-image {
  height: 300px;
  width: 300px;
  display: flex;
  justify-content: center;
  margin: auto;
}

.fab.fa-instagram {
  color: transparent;
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
  background: -webkit-radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
  background-clip: text;
  -webkit-background-clip: text;
  padding: 5px;
}

.fab.fa-twitter {
  color: #00acee;
  padding: 5px;
}

.fab.fa-linkedin-in {
  color: #0e76a8;
  padding: 5px;
}

.fab.fa-github {
  color: #171515;
  padding: 5px;
}

.fab.fa-product-hunt {
  color: #da552f;
  padding: 5px;

}

.socials {
  padding: 20px;
}

#projectButton {
  padding: 0;
  text-decoration: underline;
}

#contactButton {
  border: 1px solid black;
  border-radius: 50px;
  width: 80px;
  text-decoration: none;
  transition: box-shadow 0.3s ease-in-out;
}

#contactButton:hover {
  box-shadow: 0px 0px 10px 0px #00000045;
}

.contactFormButton {
  padding: 10px;
}

#contactForm {
  display: inline-grid;
  max-width: 400px;
  padding: 10px;
}

.contactFormInputs {
  display: inline-grid;
  padding: 10px;
}

.contactFormInputs input {
  height: auto;
  border: 1px solid black;
  border-radius: 5px;
  font-family: 'Inter', sans-serif;
  padding: 10px;
}

.contactFormInputs textarea {
  height: 25vw;
  border: 1px solid black;
  border-radius: 5px;
  font-family: 'Inter', sans-serif;
  padding: 10px;
  width: 338px;
}

.modal {
  text-align: center;
  position: absolute;
  width: 100%;
  top: 10vh;
}

.modal-header {
  width: 80vw;
  height: 80vh;
  justify-content: center;
  margin: auto;
  background: white;
  border: 1px solid black;
}
.modal-title {
  text-align: center;
  font-size: calc(10px + 3vmin);
}

.modal-closeButton {
  text-align: right;
}

#close {
  text-decoration: none;
  border: none;
  font-size: calc(10px + 3vmin);
  color: black;
  opacity: 0.5;
  font-family: 'ABeeZee', sans-serif;
  padding-right: 20px;
  padding-top: 5px;
}

#close:hover {
  opacity: 1;
}

#open {
  font-size: inherit;
  font-weight: bold;
  text-decoration: none;
  margin: 0;
  padding: 0;
}

.about-image {
  border-radius: 50%;
  height: auto;
  width: calc(100px + 5vw);
  justify-content: center;
  margin: auto;
  display: flex;
  padding: 5px;
}